/* eslint-disable */
<template>
	<div class="events">
		<b-container>
			<div class="header mt-4">
				<div class="h3 font-weight-normal">My Events</div>
			</div>
			<div class="d-flex mt-5">
				<b-dropdown right class="d-flex align-items-center" menu-class="p-0 shadow border-0" :toggle-class="`filter_btn ${!!filters.time.value && 'active'}`">
					<template #button-content>
						<b-icon icon="check2" variant="primary" class="mr-2" scale="1.2" v-if="!!filters.time.value"></b-icon>
						<b-icon icon="calendar2-week" variant="primary" class="mr-2" v-else></b-icon>
						<div class="mx-2">{{ filters.time.text }}</div>
					</template>
					<b-dropdown-item v-for="opt in timesOptions" :key="opt.value" :link-class="`p-3 ${filters.time.value == opt.value && 'active'}`" @click="filters.time = opt">
						{{ opt.text }}
					</b-dropdown-item>
				</b-dropdown>

				<b-dropdown right class="mx-3 d-flex align-items-center" menu-class="p-0 shadow border-0" :toggle-class="`filter_btn ${!!filters.link.value && 'active'}`">
					<template #button-content>
						<b-icon icon="check2" variant="primary" class="mr-2" scale="1.2" v-if="!!filters.link.value"></b-icon>
						<b-icon icon="files" variant="primary" class="mr-2" v-else></b-icon>
						<div class="mx-2">{{ filters.link.text }}</div>
					</template>
					<b-dropdown-item v-for="opt in linksOptions" :key="opt.value" :link-class="`p-3 ${filters.link.value == opt.value && 'active'}`" @click="filters.link = opt">
						{{ opt.text }}
					</b-dropdown-item>
				</b-dropdown>
			</div>
			<b-table
				:fields="table.fields"
				:items="events"
				@row-clicked="showEvent"
				:current-page="1"
				row-clicked
				responsive
				borderless
				hover
				table-class="mt-4 fs-sm"
				thead-tr-class="border-bottom text-muted"
				tbody-tr-class="cursor-pointer"
			>
				<template #cell(eventName)="{ value }">
					<div v-if="Array.isArray(value)">
						<span class="text-nowrap">{{ value[0] }}</span>
						and
						<span class="text-nowrap">{{ value[1] }}</span>
					</div>
					<div v-else>{{ value }}</div>
				</template>
			</b-table>
			<Paginate :currentPage.sync="table.currentPage" :perPage="table.perPage" :total="eventsTotal" />
		</b-container>
	</div>
</template>
<script>
	import Paginate from "@/components/Paginate";
	export default {
		components: { Paginate },

		data() {
			return {
				filters: { time: { text: "All time", value: "" }, link: { text: "All links", value: "" } },
				table: {
					fields: [
						{ label: "Date", key: "date", tdClass: "text-nowrap" },
						{ label: "Event name", key: "eventName" },
						{ label: "Invitee Email", key: "inviteeEmail" },
						{ label: "Link", key: "link", tdClass: "text-nowrap" },
					],
					perPage: 10,
					currentPage: 1,
				},
			};
		},

		computed: {
			timesOptions() {
				return this.$store.state.events.timesOptions;
			},

			linksOptions() {
				return this.$store.state.events.linksOptions;
			},

			events() {
				return this.$store.getters.events.map((event) => {
					return {
						id: event.id,
						date: this.date(event.startDate),
						eventName: this.getEventName(event),
						inviteeEmail: event.invitees.map((invitee) => invitee.email).join(", "),
						link: event.invite.name,
					};
				});
			},

			eventsTotal() {
				return this.$store.state.events.total;
			},
		},

		methods: {
			getEventName(event) {
				let eventName = event.invite.name;

				let isOneOnOne = event.invite.type == "oneOnOne";

				if (!isOneOnOne) return eventName;

				let organizer = event.invite.organizer;

				let invitee = event.invitees.find((invitee) => !invitee.isGuest);

				organizer = organizer.firstName + " " + organizer.lastName;

				return [invitee.name, organizer];
			},

			date(date) {
				return new Date(date).toLocaleDateString("en-US", {
					month: "short",
					day: "numeric",
					year: "numeric",
					hour: "numeric",
					minute: "numeric",
				});
			},

			showEvent(event) {
				this.$router.push({ name: "event", params: { id: event.id } });
			},
		},
	};
</script>
