<template>
	<div class="d-flex align-items-center justify-content-end">
		<div class="mx-2 fs-sm">{{ paginateFrom }}-{{ paginateTo }} of {{ total }}</div>
		<b-pagination hide-goto-end-buttons class="custom_pagination" v-model="page" :total-rows="total" :per-page="perPage">
			<template #prev-text> &lt; </template>
			<template #next-text> &gt; </template>
		</b-pagination>
	</div>
</template>

<script>
	export default {
		props: {
			currentPage: { type: Number, default: 1 },
			total: { type: Number, default: 1 },
			perPage: { type: Number, default: 1 },
		},

		computed: {
			page: {
				get() {
					return this.currentPage;
				},
				set(value) {
					this.$emit("update:currentPage", value);
				},
			},

			paginateTo() {
				let to = this.currentPage * this.perPage;

				if (to > this.total) {
					to = this.total;
				}

				return to;
			},

			paginateFrom() {
				return (this.currentPage - 1) * this.perPage + 1;
			},
		},
	};
</script>

<style lang="scss">
	.custom_pagination {
		margin: 0;
		user-select: none;
		li:not(:last-child):not(:first-child) {
			display: none;
		}
		.page-item {
			&:first-child,
			&:last-child {
				.page-link {
					padding: 0;
					border: none;
					font-size: 23px;
					color: #333;
					padding: 10px;
				}
				&:last-child {
					margin-left: 16px;
				}
				&.disabled {
					.page-link {
						color: #0000004f;
					}
				}
				button.page-link {
					&:focus {
						box-shadow: none;
					}
				}
			}
		}
	}
</style>
